    // subtitle_pl: "W dniach 3 i 4 lipca 2023 r. w Muzeum azienki Krlewskie odbya si midzynarodowa konferencja pod hasem Architektura Wyzwa - Odbudowa Ukrainy, organizowana przez Wydzia Architektury Politechniki Warszawskiej, Muzeum azienki Krlewskie w Warszawie i Narodowy Instytut Dziedzictwa. Konferencja bya przestrzeni wymiany myli na temat roli architektury, planowania przestrzennego i projektowania krajobrazu w obliczu zniszcze wojennych oraz przyszej odbudowy. Jej celem byo wsplne poszukiwanie odpowiedzi na pytania, jak ksztatowa rodowisko ycia wobec zagroe wojennych i jak przyszo powinna mie Ukraina po zakoczeniu dziaa zbrojnych.",
