            "On July 34, 2023, the international conference titled Architecture of Challenges  Rebuilding Ukraine was held at the azienki Krlewskie Museum in Warsaw. The event was organized by the Faculty of Architecture of the Warsaw University of Technology, the azienki Krlewskie Museum in Warsaw, and the National Heritage Board of Poland. The conference served as a platform for the exchange of ideas on the role of architecture, spatial planning, and landscape design in the context of war-related destruction and future reconstruction. Its aim was to collectively seek answers to the questions of how to shape living environments in the face of wartime threats and what kind of future Ukraine should have after the end of military operations."
