            "War destroys the achievements of civilisation, culture, and humanity. Fighting to save ones life and that of ones loved ones, ones home, and finally ones homeland - is the obvious reaction of every human being. Ones identity is determined by one belonging to the place where ones lives, ones attachment to the landscape, to the values that grow out of the indigenousness of the place - to the heritage for which meaning and memory are the basis.",
