            "Each person grew up in specific conditions, in a unique cultural environment. War shatters the social structure, brings the death of family members, and forces one to make difficult and sometimes dramatic decisions about ones and ones loved ones fate. Finding an appropriate spatial form and function to rebuild social relations is one of the most crucial issues of the post-war era. In addition to the obvious, functional answers to these questions, there is a deeper one, touching on the foundations of human identity, the understanding of the meanings of spatial forms and the cultural code.",
