            "The conference aims to bring together the academic community and the cultural, scientific, technical, economic, social activists, self-governmental, governmental and other circles engaged in rebuilding Ukraine. The subjects of the common discussion - in the context of the effects of war in Ukraine - are to be: the protection of the surviving and the reconstruction of the lost architectural heritage, the possibility of creating new living conditions, the importance of architecture in shaping identity and security. This discussion should go in line with the New European Bauhauss ideas, basing on the quality of space, supporting the complexity of the natural environment and creating a democratic human community.",
